* {
    box-sizing: border-box;
    user-select: none;
}

a {
    text-decoration: none;
}

body {
    padding: 0;
    margin: 0;
    width: 100%;
    font-family: 'Courier New', Courier, monospace;
    text-align: center;
}

#root {
    width: 100%;
    height: 100%;
    position: relative;
}

.page-wrapper {
    position: relative;
    width: 100%;
    margin: 0;
    height: auto;
}

.page {
    position: relative;
    display: flex;
    flex-direction: column;
    width: 100%;
    min-height: 100vh !important;
    margin: 0;
    z-index: 200;
}

.page-content {
    flex: 1 1 auto;
    text-align: center;
}

.page-content>h1 {
    font-family: 'Reenie Beanie', cursive;
    position: absolute;
    font-weight: 100;
    left: 50%;
    bottom: 50vh;
    font-size: 5em;
    line-height: 90%;
    transform: translate(-50%, 50%);
    text-shadow: 4px 4px 2px #101010;
}

h2 {
    margin: 0;
    padding: 20px;
}

highlight {
    color: cyan;
    font-size: 2em;
}
strong {
    position: relative;
    z-index: 200;
    color: red;
}
strong::before {
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    background: cyan;
    opacity: 0.2;
    height: 1em;
    z-index: 100;
}
.padding-lg {
    padding-left: 4em;
    padding-right: 4em;
}

p {
    text-align: justify;
    margin: 20px auto;
}

.row {
    display: flex;
    flex-direction: row;
}

.page-panel {
    position: relative;
    width: 50%;
    display: flex;
    flex-flow: column;
    scroll-behavior: smooth;  
    background-color: #ffffff; 
    transition: 1s all cubic-bezier(.77,0,.175,1);
}

.primary-bg {
    background-color: coral;
}

.dark-bg {
    background: #2d2d2d;
    color: #efefef;
}

.page-panel.left {
    position: absolute;
    top: 0;
    z-index: 200;
    min-height: 100vh;
    left: 0;
}

.page-panel.left.fixed {
    position: fixed;
}

.page-panel.right {
    position: relative;
    z-index: 100;
    left: 50%;
    overflow: auto;
    padding: 6em;
}
.page-panel.right::-webkit-scrollbar {
    width: 0px;  /* remove scrollbar space */
    background: transparent;  /* optional: just make scrollbar invisible */
}

.icon-table {
    display: flex;
    flex-flow: row wrap;
    text-align: center;
}
