.page-navbar-bottom {
    width: 100%;
    text-align: center;
    padding: 20px;
}

.page-navbar-bottom > a {
    text-align: center;
    cursor: pointer;
    margin: 10px;
    position: relative;
    color: inherit;
}

.page-navbar-bottom > a::after {
    content: '';
    width: 0;
    height: 2px;
    background: cyan;
    position: absolute;
    bottom: -5px;
    left: 50%;
    -webkit-transform: translate(-50%);
            transform: translate(-50%);
    transition: all 0.2s;
}

.page-navbar-bottom > a:hover::after {
    width: 100%;
}

* {
    box-sizing: border-box;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

a {
    text-decoration: none;
}

body {
    padding: 0;
    margin: 0;
    width: 100%;
    font-family: 'Courier New', Courier, monospace;
    text-align: center;
}

#root {
    width: 100%;
    height: 100%;
    position: relative;
}

.page-wrapper {
    position: relative;
    width: 100%;
    margin: 0;
    height: auto;
}

.page {
    position: relative;
    display: flex;
    flex-direction: column;
    width: 100%;
    min-height: 100vh !important;
    margin: 0;
    z-index: 200;
}

.page-content {
    flex: 1 1 auto;
    text-align: center;
}

.page-content>h1 {
    font-family: 'Reenie Beanie', cursive;
    position: absolute;
    font-weight: 100;
    left: 50%;
    bottom: 50vh;
    font-size: 5em;
    line-height: 90%;
    -webkit-transform: translate(-50%, 50%);
            transform: translate(-50%, 50%);
    text-shadow: 4px 4px 2px #101010;
}

h2 {
    margin: 0;
    padding: 20px;
}

highlight {
    color: cyan;
    font-size: 2em;
}
strong {
    position: relative;
    z-index: 200;
    color: red;
}
strong::before {
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    background: cyan;
    opacity: 0.2;
    height: 1em;
    z-index: 100;
}
.padding-lg {
    padding-left: 4em;
    padding-right: 4em;
}

p {
    text-align: justify;
    margin: 20px auto;
}

.row {
    display: flex;
    flex-direction: row;
}

.page-panel {
    position: relative;
    width: 50%;
    display: flex;
    flex-flow: column;
    scroll-behavior: smooth;  
    background-color: #ffffff; 
    transition: 1s all cubic-bezier(.77,0,.175,1);
}

.primary-bg {
    background-color: coral;
}

.dark-bg {
    background: #2d2d2d;
    color: #efefef;
}

.page-panel.left {
    position: absolute;
    top: 0;
    z-index: 200;
    min-height: 100vh;
    left: 0;
}

.page-panel.left.fixed {
    position: fixed;
}

.page-panel.right {
    position: relative;
    z-index: 100;
    left: 50%;
    overflow: auto;
    padding: 6em;
}
.page-panel.right::-webkit-scrollbar {
    width: 0px;  /* remove scrollbar space */
    background: transparent;  /* optional: just make scrollbar invisible */
}

.icon-table {
    display: flex;
    flex-flow: row wrap;
    text-align: center;
}

.icon {
    height: 200px;
    width: 200px;
    margin: 20px auto;
    cursor: pointer;
    transition: 0.2s all;
    position: relative;
    vertical-align: middle;
}

.icon > h4 {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    margin: 0;
    padding-top: 40%;
    background: rgba(0, 0, 0, 0.8);
    color: #efefef;
    opacity: 0;
    transition: 0.2s all;
}

.icon:hover {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
}

.icon:hover > h4 {
    opacity: 1;
}

.icon > img {
    height: 200px;
    width: auto;
    margin: 0;
    padding: 30px;
}

